.schedule-viewer-page {
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 20px;
}

.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  background: white;
  border-radius: 16px;
  padding: 20px 24px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

.header-left {
  display: flex;
  align-items: center;
  gap: 16px;
}

.back-button {
  display: flex;
  align-items: center;
  gap: var(--space-2);
  background: var(--bg-tertiary);
  border: 2px solid var(--primary-200);
  color: var(--primary-600);
  padding: var(--space-2) var(--space-4);
  border-radius: var(--radius-lg);
  font-weight: 500;
  cursor: pointer;
  transition: all var(--transition-fast);
  font-family: inherit;
  font-size: 0.9rem;
  position: relative;
}

.back-button:hover {
  background: #e2e8f0;
  color: #475569;
  transform: translateX(-2px);
}

.page-title h1 {
  margin: 0 0 4px 0;
  color: #1e293b;
  font-size: 28px;
  font-weight: 700;
}

.page-title p {
  margin: 0;
  color: #64748b;
  font-size: 16px;
}

.header-actions {
  display: flex;
  gap: 12px;
}

.edit-button,
.share-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 16px;
  border: none;
  border-radius: 8px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  font-size: 14px;
}

.edit-button {
  background: #3b82f6;
  color: white;
}

.edit-button:hover {
  background: #2563eb;
  transform: translateY(-1px);
}

.share-button {
  background: #10b981;
  color: white;
}

.share-button:hover {
  background: #059669;
  transform: translateY(-1px);
}

.schedule-content {
  background: white;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.12);
}

/* Loading State */
.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 60vh;
  background: white;
  border-radius: 16px;
  padding: 60px 40px;
  text-align: center;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.12);
}

.loading-spinner {
  width: 48px;
  height: 48px;
  border: 4px solid #f1f5f9;
  border-top: 4px solid #3b82f6;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 24px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.loading-container h3 {
  margin: 0 0 8px 0;
  color: #1e293b;
  font-size: 24px;
  font-weight: 600;
}

.loading-container p {
  margin: 0;
  color: #64748b;
  font-size: 16px;
}

/* Error State */
.error-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 60vh;
  background: white;
  border-radius: 16px;
  padding: 60px 40px;
  text-align: center;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.12);
}

.error-icon {
  font-size: 64px;
  margin-bottom: 24px;
  opacity: 0.7;
}

.error-container h3 {
  margin: 0 0 8px 0;
  color: #dc2626;
  font-size: 24px;
  font-weight: 600;
}

.error-container p {
  margin: 0 0 24px 0;
  color: #64748b;
  font-size: 16px;
  max-width: 400px;
}

/* Responsive Design */
@media (max-width: 768px) {
  .schedule-viewer-page {
    padding: 16px;
  }
  
  .page-header {
    flex-direction: column;
    gap: 16px;
    align-items: stretch;
    padding: 16px;
  }
  
  .header-left {
    justify-content: flex-start;
  }
  
  .header-actions {
    justify-content: center;
  }
  
  .page-title h1 {
    font-size: 24px;
  }
  
  .page-title p {
    font-size: 14px;
  }
  
  .edit-button,
  .share-button {
    flex: 1;
    justify-content: center;
  }
}

@media (max-width: 480px) {
  .header-left {
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
  }
  
  .back-button {
    align-self: flex-start;
  }
  
  .header-actions {
    flex-direction: column;
  }
  
  .loading-container,
  .error-container {
    padding: 40px 20px;
  }
  
  .loading-container h3,
  .error-container h3 {
    font-size: 20px;
  }
  
  .loading-container p,
  .error-container p {
    font-size: 14px;
  }
}

/* Schedule name input and save button styles */
.schedule-name-input {
  display: flex;
  align-items: center;
}

.schedule-name-field {
  padding: 10px 12px;
  border: 2px solid #e2e8f0;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  min-width: 200px;
  transition: all 0.2s ease;
  font-family: inherit;
}

.schedule-name-field:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.schedule-name-field::placeholder {
  color: #94a3b8;
}

.save-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 16px;
  border: none;
  border-radius: 8px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  font-size: 14px;
  background: #059669;
  color: white;
}

.save-button:hover:not(:disabled) {
  background: #047857;
  transform: translateY(-1px);
}

.save-button:disabled {
  opacity: 0.7;
  cursor: not-allowed;
  transform: none;
}

.save-button.saving {
  animation: pulse 2s infinite;
}

.login-prompt-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 16px;
  border: 2px solid #f59e0b;
  border-radius: 8px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  font-size: 14px;
  background: #fef3c7;
  color: #d97706;
}

.login-prompt-button:hover {
  background: #f59e0b;
  color: white;
  transform: translateY(-1px);
}

@keyframes pulse {
  0% { opacity: 1; }
  50% { opacity: 0.7; }
  100% { opacity: 1; }
}

/* Error message styles */
.error-message {
  background: #fef2f2;
  border: 1px solid #fecaca;
  color: #dc2626;
  padding: 12px 16px;
  border-radius: 8px;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 500;
}

.error-icon {
  font-size: 16px;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .header-actions {
    flex-wrap: wrap;
    gap: 8px;
  }
  
  .schedule-name-field {
    min-width: 150px;
    font-size: 12px;
  }
  
  .save-button,
  .edit-button,
  .share-button {
    font-size: 12px;
    padding: 8px 12px;
  }
}
